/**
* All general variables and functions
*/
@import "settings/_functions.scss";
@import "settings/_variables.scss";


/**
* Import bootstrap grid
*/
@import "bootstrap/_bootstrap.scss";



.preheader{    
    height: 120px;
    
    background: url(../img/wood.jpg) repeat transparent;
    background-position: top left;
    background-size: contain;
    padding-top: 20px;
    
    &__logo{
        padding-left: 0;
        padding-right: 0;
    }
    
    &__logo-ico{
        border: 10px solid #fff;        
        position: relative;
        z-index: 2;
        
        width: 100px;
        
    }
    
    &__slug-txt{ 
        margin: 0;
        
        color: #fff;
        font-size: 19px;
        font-family: serif;
        letter-spacing: 2px;
        font-weight: bold;
        line-height: 1.4;        
    }
    
    &__slug{
        padding-left: 30px;
    }
    
    &__contacts{
        margin-bottom: 0;
        padding-right: 0;
    }
    
    &__phone{
        color: #000;
        font-size: 16px;
        line-height: 1.2;
        
        &:hover, &:focus{
            color: #fff;
            text-decoration: underline;
        }
    }
    
    &__email{
        color: #18580b;
        font-size: 16px;
        line-height: 1.2;
        
        &:hover, &:focus{
            color: #fff;
            text-decoration: underline;
        }
    }
    
} // .preheader




.navbar{    
    min-height: 55px;
    border: none;
    margin: 0;
    
    background: url(../img/background.jpg) repeat transparent;
    
    .navbar-collapse{
        
        a{
            color: #000 !important;
            
            text-transform: uppercase;
            position: relative;
            
            line-height: 1.4;
            padding: 16.5px 20.7px;
            font-size: 16px;
            
            
            
        }// end a
        
        
         .dropdown-menu{    
            padding-top: 0;
            padding-bottom: 0;
            background: rgba(#f8f3f3, .8);
            padding-left: 10px;
            padding-right: 10px;

            a{    
                padding: 10px 0;

                &:after{
                    content: none !important;
                    display: none !important;
                }

                &:hover::before{
                    content: none !important;
                    display: none !important;
                }

                &:hover{
                    color: #6f6d6d !important;
                    background-color: transparent !important;
                    box-shadow: none;
                }
            }

            .divider{
                background: #b4b1b1;
                margin: 0 auto;
                width: 100%;
            }

        }// end dropdown-menu
        
    }
    
    a.nav__home{
        background: url(../img/home.png) no-repeat transparent;
        background-position: center center;
    }
    
    
   
    
    .navbar-toggle{
        border-color:#000;
        border-radius: 0;
    }
    
}// .navbar

.nav .open > a, a.active{
    background-color: #a0a0a0 !important;
    box-shadow: inset 0 1px 3px rgba(0,0,0,.75);
    
        &:before{
                content: '';
                display: block;
                position: absolute;
                background: #000;
                width: 2px;
                height: 17px;
                top: 19px;
                left: -2px;
            }
}







.jumbotron{
    background: #707070;
    padding-top: 0;
    padding-bottom: 0;
    margin-bottom: 0;
    
    >.container{
        padding-left: 0;
        padding-right: 0;
    }
    
}//.jumbotron


.carousel{
    position: relative;
    
    .item > img{
        width: 100%;
        height: auto;
    }
    
    &__control{
        display: block;
        position: absolute;
        width: 19px;
        height: 16px;
        background: url(../img/slide-arrow.png) no-repeat transparent;
        
        bottom: 10px;
        
        opacity: .7;
        
        &:hover, &:focus{
            opacity: 1;
        }
        
        &--right{
            right: 10px;
        }
        
        &--left{
            right: 36px;
            transform: rotate(-180deg);
        }
    }
    
}// carousel





.content{
    background: url(../img/background.jpg) repeat transparent;
    
    padding-top: 30px;
    padding-bottom: 30px;
    
    p{
        color: #000;
        font-size: 14px;
        letter-spacing: 2px;
        
        padding-left: 0;
        padding-right: 0;
        margin-bottom: 15px;
    }
    
    h2{
        color: #000;
        font-size: 18px;
        letter-spacing: 2px;
        font-weight: bold;
        margin-top: 25px;
        margin-bottom: 25px;
    }
    
    .img-thumbnail{
        display: block;
        padding: 10px;
        border-radius: 0;
        border: none;
        box-shadow: 0 0 20px rgba(#000, .4);
        margin: 15px auto;
    }
    
    .img-inner{
        width: 100%;
        margin-top: 0 !important;
    }
    
    .img-nom{
        margin-left: 0 !important;
        margin-right: 0 !important;
    }
    
}// .content




.footer{
    background: url(../img/wood.jpg) repeat transparent;
    background-position: top left;
    background-size: contain;
    
    padding-top: 15px;
    padding-bottom: 0;
    
    &__tag-cloud{
        padding-left: 0;
        padding-right: 0;
    }
    
    &__tag-link{
        color: #fff;
        font-size: 14px;
        text-decoration: underline;
        padding: 5px;
        
        letter-spacing: 1.8px;
        line-height: 1.7;
        
        &:hover, &:focus{
            color: #000;
        }
    }
    
    
    &__extern{
        color: #000;
        font-size: 16px;
    }
    
    &__separator{
        border-top-color: #000;
        margin: 10px 0;
    }
    
    &__contacts{
        padding-left: 0;
        padding-right: 0;
        text-align: center;
        margin-top: 10px;
    }
    
}// .footer




/**
* Media queries rules (mobile first approach)
*/
@import "responsive/_media-queries.scss";

