.preheader {
  height: 120px;
  background: url(../img/wood.jpg) repeat transparent;
  background-position: top left;
  background-size: contain;
  padding-top: 20px;
}

.preheader__logo {
  padding-left: 0;
  padding-right: 0;
}

.preheader__logo-ico {
  border: 10px solid #fff;
  position: relative;
  z-index: 2;
  width: 100px;
}

.preheader__slug-txt {
  margin: 0;
  color: #fff;
  font-size: 19px;
  font-family: serif;
  letter-spacing: 2px;
  font-weight: bold;
  line-height: 1.4;
}

.preheader__slug {
  padding-left: 30px;
}

.preheader__contacts {
  margin-bottom: 0;
  padding-right: 0;
}

.preheader__phone {
  color: #000;
  font-size: 16px;
  line-height: 1.2;
}

.preheader__phone:hover, .preheader__phone:focus {
  color: #fff;
  text-decoration: underline;
}

.preheader__email {
  color: #18580b;
  font-size: 16px;
  line-height: 1.2;
}

.preheader__email:hover, .preheader__email:focus {
  color: #fff;
  text-decoration: underline;
}

.navbar {
  min-height: 55px;
  border: none;
  margin: 0;
  background: url(../img/background.jpg) repeat transparent;
}

.navbar .navbar-collapse a {
  color: #000 !important;
  text-transform: uppercase;
  position: relative;
  line-height: 1.4;
  padding: 16.5px 20.7px;
  font-size: 16px;
}

.navbar .navbar-collapse .dropdown-menu {
  padding-top: 0;
  padding-bottom: 0;
  background: rgba(248, 243, 243, 0.8);
  padding-left: 10px;
  padding-right: 10px;
}

.navbar .navbar-collapse .dropdown-menu a {
  padding: 10px 0;
}

.navbar .navbar-collapse .dropdown-menu a:after {
  content: none !important;
  display: none !important;
}

.navbar .navbar-collapse .dropdown-menu a:hover::before {
  content: none !important;
  display: none !important;
}

.navbar .navbar-collapse .dropdown-menu a:hover {
  color: #6f6d6d !important;
  background-color: transparent !important;
  box-shadow: none;
}

.navbar .navbar-collapse .dropdown-menu .divider {
  background: #b4b1b1;
  margin: 0 auto;
  width: 100%;
}

.navbar a.nav__home {
  background: url(../img/home.png) no-repeat transparent;
  background-position: center center;
}

.navbar .navbar-toggle {
  border-color: #000;
  border-radius: 0;
}

.nav .open > a, a.active {
  background-color: #a0a0a0 !important;
  box-shadow: inset 0 1px 3px rgba(0, 0, 0, 0.75);
}

.nav .open > a:before, a.active:before {
  content: '';
  display: block;
  position: absolute;
  background: #000;
  width: 2px;
  height: 17px;
  top: 19px;
  left: -2px;
}

.jumbotron {
  background: #707070;
  padding-top: 0;
  padding-bottom: 0;
  margin-bottom: 0;
}

.jumbotron > .container {
  padding-left: 0;
  padding-right: 0;
}

.carousel {
  position: relative;
}

.carousel .item > img {
  width: 100%;
  height: auto;
}

.carousel__control {
  display: block;
  position: absolute;
  width: 19px;
  height: 16px;
  background: url(../img/slide-arrow.png) no-repeat transparent;
  bottom: 10px;
  opacity: .7;
}

.carousel__control:hover, .carousel__control:focus {
  opacity: 1;
}

.carousel__control--right {
  right: 10px;
}

.carousel__control--left {
  right: 36px;
  transform: rotate(-180deg);
}

.content {
  background: url(../img/background.jpg) repeat transparent;
  padding-top: 30px;
  padding-bottom: 30px;
}

.content p {
  color: #000;
  font-size: 14px;
  letter-spacing: 2px;
  padding-left: 0;
  padding-right: 0;
  margin-bottom: 15px;
}

.content h2 {
  color: #000;
  font-size: 18px;
  letter-spacing: 2px;
  font-weight: bold;
  margin-top: 25px;
  margin-bottom: 25px;
}

.content .img-thumbnail {
  display: block;
  padding: 10px;
  border-radius: 0;
  border: none;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.4);
  margin: 15px auto;
}

.content .img-inner {
  width: 100%;
  margin-top: 0 !important;
}

.content .img-nom {
  margin-left: 0 !important;
  margin-right: 0 !important;
}

.footer {
  background: url(../img/wood.jpg) repeat transparent;
  background-position: top left;
  background-size: contain;
  padding-top: 15px;
  padding-bottom: 0;
}

.footer__tag-cloud {
  padding-left: 0;
  padding-right: 0;
}

.footer__tag-link {
  color: #fff;
  font-size: 14px;
  text-decoration: underline;
  padding: 5px;
  letter-spacing: 1.8px;
  line-height: 1.7;
}

.footer__tag-link:hover, .footer__tag-link:focus {
  color: #000;
}

.footer__extern {
  color: #000;
  font-size: 16px;
}

.footer__separator {
  border-top-color: #000;
  margin: 10px 0;
}

.footer__contacts {
  padding-left: 0;
  padding-right: 0;
  text-align: center;
  margin-top: 10px;
}

/**
* Media queries rules (mobile first approach)
*/
@media only screen and (min-width: 480px) {
  .preheader__phone, .preheader__email {
    font-weight: bold;
  }
}

@media only screen and (min-width: 768px) {
  .preheader {
    padding-top: 30px;
  }
  .preheader__logo-ico {
    width: auto;
  }
  .jumbotron > .container {
    padding-left: 15px;
    padding-right: 15px;
  }
  .content p {
    font-size: 18px;
    letter-spacing: 2px;
  }
  .content p.col-md-4 {
    padding-left: 15px;
    padding-right: 15px;
  }
  .content h2 {
    font-size: 24px;
    letter-spacing: 2px;
  }
  .content .img-thumbnail {
    display: inline-block;
    margin: 15px 30px;
  }
  .content .img-inner {
    width: auto;
    max-width: 50%;
  }
  .content .img-inner--left {
    margin-left: 0 !important;
    float: left;
  }
  .content .img-inner--right {
    margin-right: 0 !important;
    float: right;
  }
  .footer__contacts--phones {
    text-align: right;
  }
  .navbar .navbar-collapse {
    background-color: #dcdcdc;
    box-shadow: 0 1px 6px rgba(0, 0, 0, 0.75);
    padding-left: 182px;
    padding-right: 0;
  }
  .navbar .navbar-collapse a:after {
    content: '';
    display: block;
    position: absolute;
    background: #000;
    width: 2px;
    height: 17px;
    top: 19px;
    right: -2px;
  }
  .navbar .navbar-collapse a:hover {
    background-color: #a0a0a0 !important;
    box-shadow: inset 0 1px 3px rgba(0, 0, 0, 0.75);
  }
  .navbar .navbar-collapse a:hover::before {
    content: '';
    display: block;
    position: absolute;
    background: #000;
    width: 2px;
    height: 17px;
    top: 19px;
    left: -2px;
  }
  .navbar .navbar-collapse a.no-l:hover::before {
    content: none;
    display: none;
  }
  .navbar .navbar-collapse a.no-l:before {
    content: none !important;
    display: none !important;
  }
  .navbar .navbar-collapse a.no-r:after {
    content: none;
    display: none;
  }
}

@media only screen and (min-width: 996px) {
  .content p {
    font-size: 18px;
  }
  .content h2 {
    font-size: 24px;
  }
  .preheader, .footer {
    background-size: auto;
  }
  .navbar .navbar-collapse a {
    line-height: 1;
    padding: 16.5px 30px;
    font-size: 22px;
    padding: 16.5px 30.3px;
  }
  .preheader__slug {
    padding-left: 60px;
  }
}
