// ***** 480px *****
@media only screen and (min-width : $mobile--lg) {    
  
    .preheader{
        
        &__phone, &__email{            
            font-weight: bold;
        }
        
    }
        
}// end media query 480px








// ***** 768px *****
@media only screen and (min-width : $tablet) {
    
    .preheader{ 
        padding-top: 30px;
        
        &__logo-ico{
            width: auto;
        }
        
    }
    
    
    .jumbotron{
        >.container{
            padding-left: 15px;
            padding-right: 15px;
        }

    }//.jumbotron
    
    
    .content{

        p{
            font-size: 18px;
            letter-spacing: 2px;
            
            &.col-md-4{
                padding-left: 15px;
                padding-right: 15px;
            }
        }

        h2{
            font-size: 24px;
            letter-spacing: 2px;
        }
        
        .img-thumbnail{
            display: inline-block;
            margin: 15px 30px;
        }
        
        .img-inner{
            width: auto;
            max-width: 50%;
            
            &--left{
                margin-left: 0 !important;
                float: left;
            }
            
            &--right{
                margin-right: 0 !important;
                float: right;
            }
        }

    }// .content
    
    
    .footer{
        
        &__contacts--phones{
           text-align: right;
        }
    }
    
    
    
    .navbar{ 
        .navbar-collapse{
            background-color: #dcdcdc;
            box-shadow: 0 1px 6px rgba(0,0,0,.75);
            
            padding-left: 182px;
            padding-right: 0;
           
        
                a{
                    &:after{
                        content: '';
                        display: block;
                        position: absolute;
                        background: #000;
                        width: 2px;
                        height: 17px;
                        top: 19px;
                        right: -2px;
                    }

                    &:hover{
                        background-color: #a0a0a0 !important;
                        box-shadow: inset 0 1px 3px rgba(0,0,0,.75);
                    }

                    &:hover::before{
                        content: '';
                        display: block;
                        position: absolute;
                        background: #000;
                        width: 2px;
                        height: 17px;
                        top: 19px;
                        left: -2px;
                    }

                    &.no-l:hover::before{
                        content: none;
                        display: none;
                    }

                    &.no-l:before{
                        content: none !important;
                        display: none !important;
                    }

                    &.no-r:after{
                        content: none;
                        display: none;
                    }


                }// end a
    
            
        }
        
    } //. navbar
    
    
}// end media query 768px








// ***** 996px *****
@media only screen and (min-width : $desktop--sm) {
    
    
    .content{

        p{
            font-size: 18px;
        }

        h2{
            font-size: 24px;
        }
        
        

    }// .content
    
    .preheader, .footer{  
        background-size: auto;
    }
    
    
    
    .navbar{    
        .navbar-collapse{
            a{
                line-height: 1;
                padding: 16.5px 30px;
                font-size: 22px;
                padding: 16.5px 30.3px;



            }// end a
        }
    }
    
    
    .preheader{
        &__slug{
            padding-left: 60px;
        }
    }
   
}// end media query 996px







